	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"http://blogs.technet.com/b/microsoft_on_the_issues/archive/2013/08/15/the-limits-of-google-s-openness.aspx\">From Microsoft’s official “Microsoft on the Issues” Blog:</a></p>\n<blockquote>\n<p>It seems to us that Google’s reasons for blocking our app are manufactured so that we can’t give our users the same experience Android and iPhone users are getting. The roadblocks Google has set up are impossible to overcome, and they know it.</p>\n</blockquote>\n<p>It’s ok to have an open API, and it’s ok to not have an open API so that your competitors can’t benefit from your services, but claiming to have an open API and then shutting out users of a competitors platforms on a string of technicalities? That’s just a joke.</p>","frontmatter":{"title":"Somewhat Open","keywords":null,"category":"opinion","date":"2013-08-15T16:15:00+00:00","path":"/2013/08/15/somewhat-open","layout":"post","hideFooter":null},"fields":{"slug":"/2013/08/15/somewhat-open"}}},"pathContext":{"slug":"/2013/08/15/somewhat-open","relatedPosts":[{"path":"/2012/11/09/bayesian-witch-hunt","data":{"title":"Bayesian Witch Hunt","path":"/2012/11/09/bayesian-witch-hunt","description":"A nerd tribute from a friend","date":"2012-11-09T12:00:00+00:00"}},{"path":"/2012/12/29/i-hate-computing-ecosystems","data":{"title":"I hate computing ecosystems","path":"/2012/12/29/i-hate-computing-ecosystems","description":"Why does nothing work together?","date":"2012-12-29T23:06:00+00:00"}},{"path":"/2012/12/25/medium-the-end-of-history-and-the-last-website","data":{"title":"Medium: The end of History and the last website","path":"/2012/12/25/medium-the-end-of-history-and-the-last-website","description":"Is there any use in personal websites anymore?","date":"2012-12-25T21:00:00+00:00"}}]}}

